.carousel {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  margin-top: 50px;
}

.slide {
  border-radius: 0.5rem;
  box-shadow: 0px 0px 7px #666;
  width: 100%;
  height: 100%;
}

.slide-hidden {
  display: none;
}

.arrow {
  position: absolute;
  filter: drop-shadow(0px 0px 5px #555);
  width: 2rem;
  height: 2rem;
  color: white;
}

.arrow:hover {
  cursor: pointer;
}

.arrow-left {
  left: 1rem;
}

.arrow-right {
  right: 1rem;
}

.indicators {
  display: flex;
  position: absolute;
  bottom: 1rem;
}

.indicator {
  background-color: white;
  height: 12px;
  width: 12px;
  border-radius: 100%;
  border: none;
  outline: none;
  box-shadow: 0px 0px 5px #555;
  margin: 0 0.2rem;
  cursor: pointer;
}

.indicator-inactive {
  background-color: grey;
}

#nav {
  display: inline-block;
  margin-top: 0px;
}

#nav>li {
  display: inline-block;
}

#nav li {
  position: relative;
}

#nav>li>a {
  color: white;
  text-transform: uppercase;
  font-family: Roboto;
}

#nav li a {
  text-decoration: none;
  line-height: 50px;
  display: block;
}

#nav .subnav li:hover,
#nav>li:hover>a {
  color: #0066bf;
  background-color: white;
}

#nav li:hover .subnav {
  display: block;
}

#nav,
.subnav {
  list-style-type: none;
}

#nav .subnav {
  position: absolute;
  background-color: #fff;
  display: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  top: 100%;
  left: 0;
  width: 390px;
  height: 245px;
}

#nav .subnav .boxA {
  color: #000;
  line-height: 50px;
  padding: 0 20px;
}

.boxA:hover {
  background-color: #0066bf;
  color: white;
}

.footer {
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #0066BF;
  color: white;
  height: 100%;
  padding-left: -1110px;
}

.footerMobile {
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #0066BF;
  color: white;
  height: 400px;
  padding-left: -1110px;
}

.photo {
  overflow: hidden;
}

.imgHover {
  transition: transform 0.25s;
}

.photo:hover .imgHover {
  transform: scale(1.1);
}

.table {
  border-collapse: collapse;
  margin-top: 50px;
  border: 1px solid black;
  max-width: 1180px;
  width: 100%;
}

.th {
  border: 1px solid black;
  height: 50px
}

.td {
  border: 1px solid black;
}

.all {
  display: block;
}

.mypage-alo-phone {
  position: fixed !important;
  bottom: -10px !important;
  background-color: transparent;
  width: 110px;
  height: 110px;
  cursor: pointer;
  z-index: 900000 !important;
}

.animated.infinite {
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

.pulse {
  -webkit-animation-name: pulse;
  animation-name: pulse;
}

.tada {
  -webkit-animation-name: tada;
  animation-name: tada;
}

.mypage-alo-ph-img-circle {
  background-color: #c20000;
}

.mypage-alo-ph-circle {
  width: 70px;
  height: 70px;
  top: 22px;
  left: 22px;
  position: absolute;
  background-color: #00a308;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%;
  opacity: 1;
}

.mypage-alo-phone span {
  background-color: #00a308;
}

.mypage-alo-ph-img-circle {
  width: 30px;
  height: 30px;
  top: 43px;
  left: 43px;
  position: absolute;
  background: rgba(30, 30, 30, 0.1) url(data:image/png;base64,) no-repeat center center;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%;
  border: 2px solid transparent;
  opacity: 1;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  -webkit-transform-origin: 50% 50%;
  -moz-transform-origin: 50% 50%;
  -ms-transform-origin: 50% 50%;
  -o-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  background-size: 70%;
}

.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.zoomIn {
  -webkit-animation-name: zoomIn;
  animation-name: zoomIn;
}

.mypage-alo-phone span {
  font-size: 16px;
  color: #fff;
  padding: 10px 0 10px 5px;
  position: absolute;
  left: 78px;
  top: 36px;
  width: 135px;
  border-radius: 0 25px 25px 0;
  font-weight: bold;
  letter-spacing: 1px;
  /* opacity: .4 !important; */
  transition: all .5s;
}

/* Mobile */

.nav-elements {
  background-color: #0066BF;
  width: 100%;
  max-height: 500px;
  transition: all 0.5s ease-in-out;
  overflow: hidden;
}

.nav-elements.active {
  width: 100%;
}

.nav-elements ul {
  display: flex;
  flex-direction: column;
}

.nav-elements ul li {
  list-style: none;
}

.nav-products {
  background-color: #0066BF;
  width: 100%;
  height: 170px;
  transition: all 0.5s ease-in-out;
  overflow: hidden;
  margin-left: 0px;
}

.nav-products.active {
  width: 100%;
}

.nav-products ul {
  display: flex;
  flex-direction: column;
}

.nav-products ul li {
  list-style: none;
}